:root {
    --heading-size: clamp(2rem, 5vw, 3rem);
    --subheading-size: clamp(1.5rem, 4vw, 2.5rem);
    --transition-base: all 0.3s ease;
    --shadow-base: 0 10px 30px rgba(0, 0, 0, 0.1);
    --shadow-hover: 0 10px 20px rgba(255, 135, 15, 0.2);
    --border-radius: 12px;
    --border-radius-large: 50px;
}

/* Animations */
@keyframes floatHero {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes badgeFloatHero {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }
}

@keyframes btnShine {
    100% {
        left: 200%;
    }
}

/* Main Layout */
.hero {
    position: relative;
    overflow: hidden;
    background: linear-gradient(135deg, #fff5f2 0%, #fff 100%);
    padding: clamp(5rem, 8vw, 10rem) 1rem 2rem;
    min-height: 78vh;
}

.hero-inner {
    display: flex;
    align-items: center;
    min-height: 50vh;
    justify-content: space-between;
    gap: clamp(2rem, 5vw, 4rem);
    max-width: 1200px;
    margin: 0 auto;
}

/* Hero Left Section */
.hero-left {
    width: 100%;
    text-align: left;
}

.hero-badge {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: white;
    padding: 0.5rem 1rem;
    border-radius: var(--border-radius-large);
    font-size: 1rem;
    margin-bottom: 1.5rem;
    animation: badgeFloatHero 3s ease-in-out infinite;
}

.hero-left-text-heading {
    color: var(--text-dark);
    font-size: var(--heading-size);
    margin-bottom: 1.5rem;
    line-height: 1.2;
    opacity: 0;
    animation: slideUp 0.6s ease-out 0.3s forwards;
}

.hero-left-text-subheading {
    font-size: var(--subheading-size);
    line-height: 1.4;
    margin-bottom: 2rem;
    color: var(--primary-color);
}

/* Text Effects */
.highlight {
    color: var(--primary-color);
}

.gradient-text {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Buttons */
.hero-buttons {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
    opacity: 0;
    animation: slideUp 0.6s ease-out 0.6s forwards;
}

.primary-btn,
.secondary-btn {
    padding: 1rem 2rem;
    border-radius: var(--border-radius-large);
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: var(--transition-base);
    position: relative;
    overflow: hidden;
}

.primary-btn {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: white;
    border: none;
}

.secondary-btn {
    background: transparent;
    border: 2px solid var(--primary-color);
    color: var(--primary-color);
}

.btn-shine {
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(120deg, transparent, rgba(255, 255, 255, 0.3), transparent);
    animation: btnShine 3s linear infinite;
}

.btn-arrow {
    display: inline-block;
    margin-left: 0.5rem;
    transition: transform 0.3s ease;
}

/* Button Hover Effects */
.primary-btn:hover,
.secondary-btn:hover {
    transform: translateY(-3px);
    box-shadow: var(--shadow-hover);
}

.secondary-btn:hover {
    background: var(--primary-color);
    color: white;
}

.secondary-btn:hover .btn-arrow {
    transform: translateX(5px);
}

/* Stats Section */
.hero-stats {
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
    opacity: 0;
    animation: fadeIn 0.6s ease-out 0.9s forwards;
}

.stat-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.stat-number {
    font-size: 2.5rem;
    font-weight: bold;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.stat-label {
    color: var(--text-dark);
    font-size: 1rem;
}

/* Hero Right Section */
.hero-right {
    width: 100%;
}

.image-wrapper {
    position: relative;
    width: 100%;
    opacity: 0;
    animation: fadeIn 0.6s ease-out 1.2s forwards;
}

/* Carousel Styles */
.hero-carousel {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    position: relative;
}

.carousel-slide {
    padding: 5px;
}

.carousel-slide img {
    width: 97%;
    height: 600px;
    object-fit: cover;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow-base);
    transition: var(--transition-base);
}

.carousel-slide:hover img {
    transform: scale(1.02);
}

/* Carousel Navigation */

/* Floating Cards */
.floating-card {
    position: absolute;
    background: rgba(255, 255, 255, 0.9);
    padding: 10px 20px;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow-base);
    backdrop-filter: blur(5px);
    z-index: 2;
    animation: floatHero 3s ease-in-out infinite;
}

.card-1 {
    top: 10%;
    left: -10%;
}

.card-2 {
    top: 30%;
    right: -10%;
}

.card-3 {
    bottom: 30%;
    left: -10%;
}

.card-4 {
    bottom: 10%;
    right: -10%;
}

/* Background Elements */
.animated-circle {
    position: absolute;
    border-radius: 50%;
    opacity: 0.1;
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    filter: blur(40px);
}

.circle-1 {
    width: 400px;
    height: 400px;
    top: -100px;
    left: -100px;
    animation: floatHero 8s ease-in-out infinite;
}

.circle-2 {
    width: 300px;
    height: 300px;
    bottom: -50px;
    right: -50px;
    animation: floatHero 8s ease-in-out infinite -2s;
}

.circle-3 {
    width: 200px;
    height: 200px;
    top: 50%;
    left: 50%;
    animation: floatHero 8s ease-in-out infinite -4s;
}

/* Responsive Layouts */
@media screen and (max-width: 1024px) {
    .hero-inner {
        flex-direction: column;
        text-align: center;
    }

    .hero-left {
        text-align: center;
    }

    .hero-stats {
        justify-content: center;
    }

    .floating-card {
        display: none;
    }

    .carousel-slide img {
        height: 500px;
    }

    .slick-prev {
        left: -30px;
    }

    .slick-next {
        right: -30px;
    }
}

@media screen and (max-width: 768px) {
    .hero {
        padding-top: 8rem;
    }

    .hero-carousel {
        padding: 10px;
    }

    .carousel-slide img {
        height: 400px;
    }

    .hero-buttons {
        flex-direction: column;
        max-width: 300px;
        margin: 2rem auto 0;
    }

    .hero-stats {
        flex-direction: column;
        gap: 1rem;
    }

    .stat-number {
        font-size: 2rem;
    }

    .stat-label {
        font-size: 0.9rem;
    }

    /* .animated-circle,
    .btn-shine {
        display: none !important;
    } */



    .floating-card {
        padding: 8px 15px;
        font-size: 14px;
    }
}

